<template>
    <v-container id="template" >
        <v-btn id="print" class="blue white--text" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="ml-4 red white--text" to="/hbelipembelian" link>Back</v-btn>
       
       <template id="template">
           <br><h2 class="text-center">Pembelian</h2><br>
            <table id="table" >
                <thead>
                    <tr>
                        <th id="th">Tanggal</th>
                        <th id="th">No Bukti</th>
                        <th id="th">Nama Suplier</th>
                        <th id="th">Nama Barang</th>
                        <th id="th">Quantity</th>
                        <th id="th">Harga</th>
                        <th id="th">Sub Total</th>
                        <th id="th">Total</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in pembelians" :key='item.id'>
                        <td id="td" width="100px" class="text-center"><pre>{{item["Tanggal"]}}</pre></td>
                        <td id="td" width="80px" class="text-center"><pre>{{item["No Bukti"]}}</pre></td>
                        <td id="td" class="text-center"><pre>{{item["Nama Suplier"]}}</pre></td>
                        <td id="td" ><pre>{{item["Nama Barang"]}}</pre></td>
                        <td id="td" width="100px" class="text-right"><pre>{{item["Quantity"]}}</pre></td>
                        <td id="td" width="100px" class="text-right"><pre>{{item["Harga"] }}</pre></td>
                        <td id="td" width="120px" class="text-right"><pre>{{item["Sub Total"]}}</pre></td>
                        <td id="td" width="150px" class="text-right"><pre>{{item["Total"]}}</pre></td>
                    </tr>
                    <tr>
                        <td colspan="7" id="td" class="text-center"><pre><b>Total Pembelian</b></pre></td>
                        <td  id="td" colspan="2" class="text-right"><pre><b>{{this.totalpembelians}}</b></pre></td>
                    </tr>
                </tbody>
            </table>
       </template>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                keyword: '',
                load: false,
                headers: [{
                        text: 'No',
                        value: 'no',
                    },
                    {
                        text: 'Tanggal',
                        value: 'Tanggal'
                    },
                    {
                        text: 'No Bukti',
                        value: 'No Bukti'
                    },
                    {
                        text: 'Nama Suplier',
                        value: 'Nama Suplier'
                    },
                    {
                        text: 'Nama Barang',
                        value: 'Nama Barang'
                    },
                    {
                        text: 'Quantity',
                        value: 'Quantity'
                    },
                    {
                        text: 'Harga',
                        value: 'Harga'
                    },
                    {
                        text: 'Sub Total',
                        value: 'Sub Total'
                    },
                    {
                        text: 'Total',
                        value: 'Total'
                    },
                ],
                pembelians: [],
                totalpembelians: '',

                total :0,
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'hbelipembelians/report'
                this.$http.get(uri).then(response => {
                    this.pembelians = response.data.data
                    this.totalpembelians = response.data.total[0].TotalAll
        
                })
            },

            
            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style>
    @media print{
        #print{
            display: none;
        }
        #template{
            background-color: white
        }
    }
    
    #table, #th, #td {
        border: 2px solid black;
        border-collapse: collapse;
    }
</style>